.mat-chip-list-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  /*
   * Only apply the margins to chips
   */ }
  .mat-chip-list-wrapper .mat-chip:not(.mat-basic-chip) {
    margin: 0 3px 0 3px; }
    .mat-chip-list-wrapper .mat-chip:not(.mat-basic-chip):first-child {
      margin-left: 0;
      margin-right: 3px; }
      [dir='rtl'] .mat-chip-list-wrapper .mat-chip:not(.mat-basic-chip):first-child {
        margin-left: 3px;
        margin-right: 0; }
    .mat-chip-list-wrapper .mat-chip:not(.mat-basic-chip):last-child {
      margin-left: 3px;
      margin-right: 0; }
      [dir='rtl'] .mat-chip-list-wrapper .mat-chip:not(.mat-basic-chip):last-child {
        margin-left: 0;
        margin-right: 3px; }

.mat-chip:not(.mat-basic-chip) {
  display: inline-block;
  padding: 8px 12px 8px 12px;
  border-radius: 24px;
  font-size: 13px;
  line-height: 16px; }

.mat-chip-list-stacked .mat-chip-list-wrapper {
  display: block; }
  .mat-chip-list-stacked .mat-chip-list-wrapper .mat-chip:not(.mat-basic-chip) {
    display: block;
    margin: 0;
    margin-bottom: 8px; }
    [dir='rtl'] .mat-chip-list-stacked .mat-chip-list-wrapper .mat-chip:not(.mat-basic-chip) {
      margin: 0;
      margin-bottom: 8px; }
    .mat-chip-list-stacked .mat-chip-list-wrapper .mat-chip:not(.mat-basic-chip):last-child, [dir='rtl'] .mat-chip-list-stacked .mat-chip-list-wrapper .mat-chip:not(.mat-basic-chip):last-child {
      margin-bottom: 0; }
